<template>
  <div class="pagination">
    <button
      @click="getNumberPage"
      class="pagination-item"
      v-for="n in quantityPage"
      :key="n"
    >
      {{ n }}
    </button>
  </div>
</template>

<script>
export default {
  name: "CostPagination",
  props: {
    quantityCost: [],
  },
  data() {
    return {
      limitItemPage: 5,
      quantityPage: [],
    };
  },
  created() {
    this.quantityPage = this.getQuantityPage;
  },
  updated() {
    this.quantityPage = this.getQuantityPage;
  },
  computed: {
    getQuantityPage() {
      return Math.ceil(this.quantityCost / this.limitItemPage);
    },
  },
  methods: {
    getNumberPage(i) {
      this.$emit("NumberPage", i.target.innerText);
      console.log(i.target.innerText);
    },
  },
};
</script>

<style lang="scss" type="scoped">
.pagination {
  display: flex;
  justify-content: space-around;
  width: 500px;
  align-self: center;
  padding-top: 40px;
}
.pagination-item {
  width: 30px;
  height: 30px;
}
</style>