<template>
  <div>
    <div class="payment-row" v-for="item in renderList" :key="item">
      <div class="payment-item">{{ item.room }}</div>
      <div class="payment-item">{{ item.date }}</div>
      <div class="payment-item">{{ item.category }}</div>
      <div class="payment-item">{{ item.value }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PaymentList",
  props: {
    items: [],
    page: [],
  },
  data() {
    return {
      limitItemPage: 5,
    };
  },

  computed: {
    renderList() {
      const start = (this.page - 1) * this.limitItemPage;
      const end = start + this.limitItemPage;
      return this.items.slice(start, end);
    },
  },
};
</script>

